import React from 'react'

import Input from '@material-ui/core/Input'
import InputLabel from '@material-ui/core/InputLabel'
import FormHelperText from '@material-ui/core/FormHelperText'
import FormControl from '@material-ui/core/FormControl'
import InputAdornment from '@material-ui/core/InputAdornment'
import Select from '@material-ui/core/Select'
import MenuItem from '@material-ui/core/MenuItem'

import CloseIcon from '@material-ui/icons/Close'
import CheckIcon from '@material-ui/icons/Check'

export const renderField = (props) => {
  const {
    fieldType = 'text',
    required,
    meta: { touched, invalid }
  } = props

  return (
    <FormControl
      fullWidth
      required={required}
      error={touched && invalid}
      style={{margin: '.5rem 0'}}>
      {fieldType === 'text' && <CustomTextField {...props} />}
      {fieldType === 'select' && <CustomSelectField {...props} />}
    </FormControl>
  )
}

const CustomTextField = (props) => {
  const {
    input,
    label,
    type,
    disabled,
    meta: {touched, error, invalid},
    inputComponent,
    startAdornment
  } = props

  return (
    <div>
      {
        startAdornment
          ? <InputLabel htmlFor={input.name} shrink>{label}</InputLabel>
          : <InputLabel htmlFor={input.name}>{label}</InputLabel>
      }
      <Input
        {...input}
        fullWidth
        type={type}
        disabled={disabled}
        inputComponent={inputComponent}
        startAdornment={
          startAdornment && <InputAdornment position='start'>{startAdornment}</InputAdornment>
        }
        endAdornment={
          touched &&
          <InputAdornment position='end'>
            {invalid ? <CloseIcon color='error' /> : <CheckIcon style={{color: 'green'}} />}
          </InputAdornment>
        } />
      {touched && error && <FormHelperText error>{error}</FormHelperText>}
    </div>
  )
}

const CustomSelectField = (props) => {
  const {
    input,
    label,
    meta: {touched, error, invalid},
    optionList = [],
    onSelectChange
  } = props

  return (
    <div>
      <InputLabel htmlFor={input.name} shrink>{label}</InputLabel>
      <Select
        {...input}
        fullWidth
        displayEmpty
        disabled={optionList.length === 0}
        inputProps={{
          name: input.name,
          id: input.name
        }}
        onChange={(event) => {
          input.onChange(event.target.value)
          onSelectChange && onSelectChange(event)
        }}
        endAdornment={
          touched &&
          <InputAdornment position='end'>
            {invalid ? <CloseIcon color='error' /> : <CheckIcon style={{color: 'green'}} />}
          </InputAdornment>
        }>
        <MenuItem value=''>
          <em>None</em>
        </MenuItem>
        {
          optionList.map((option, index) => (
            <MenuItem key={index} value={option.value}>{option.name}</MenuItem>
          ))
        }
      </Select>
      {touched && error && <FormHelperText error>{error}</FormHelperText>}
    </div>
  )
}



