import React, {Component} from 'react'
import { withStyles } from '@material-ui/core/styles'
import { connect } from 'react-redux'

import Grid from '@material-ui/core/Grid'
import CircularProgress from '@material-ui/core/CircularProgress'
import Stepper from '@material-ui/core/Stepper'
import Step from '@material-ui/core/Step'
import StepLabel from '@material-ui/core/StepLabel'
import StepContent from '@material-ui/core/StepContent'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'

import CheckIcon from '@material-ui/icons/Check'

import CreateAccount from './CreateAccount/CreateAccount'
import GeneralInfomation from './GeneralInfomation/GeneralInfomation'
import UploadAvator from './UploadAvator/UploadAvator'

const styles = theme => ({
  root: {},
  loader: {
    margin: '5rem',
    textAlign: 'center'
  },
  resetContainer: {
    padding: theme.spacing.unit * 3
  }
})

class SigninContent extends Component {
  constructor () {
    super()
    this.state = {
      activeStep: 0
    }
  }

  handleNext = () => {
    this.setState(state => ({
      activeStep: state.activeStep + 1
    }))
  }

  handleBack = () => {
    this.setState(state => ({
      activeStep: state.activeStep - 1
    }))
  }

  handleReset = () => {
    this.setState({
      activeStep: 0
    })
  }

  handleSubmit = (values) => {
    const {
      createAccount = {},
      generalInfomation = {},
      uploadAvator = {values: {}},
      addNewRegister
    } = this.props
    const isUploadAvatorSuccess = uploadAvator.values && (Object.keys(uploadAvator.values).length > 0)

    this.handleNext()
    if (!isUploadAvatorSuccess) {
      return
    }

    const payload = {
      ...createAccount.values,
      ...generalInfomation.values,
      ...uploadAvator.values,
      timestamp: Date.now()
    }

    addNewRegister(payload)
  }

  render () {
    const {activeStep} = this.state
    const {
      classes,
      isDataLoaded,
      isRegisterSuccess
    } = this.props
    const contentProps = {
      onSubmit: this.handleSubmit,
      handleBack: this.handleBack,
      activeStep: activeStep
    }
    const stepList = [
      {
        title: 'Create Account',
        content: 'Glad to see you here!',
        component: <CreateAccount {...contentProps} />
      }, {
        title: 'General Infomation',
        content: 'Tell us who you are!',
        component: <GeneralInfomation {...contentProps} />
      }, {
        title: 'Upload Avator',
        content: 'We wanna know you more!',
        component: <UploadAvator handleUploadChange={this.handleUploadChange} {...contentProps} />
      }
    ]

    return (
      <div>
        <Grid container justify='center'>
          <Grid item xs={12}>
            {
              !isDataLoaded &&
              <div className={classes.loader}>
                <CircularProgress size={80} />
              </div>
            }
            {
              isDataLoaded &&
              <div className={classes.root}>
                <Stepper activeStep={activeStep} orientation='vertical'>
                  {stepList.map((step, index) => {
                    return (
                      <Step key={index}>
                        <StepLabel>{step.title}</StepLabel>
                        <StepContent>
                          {step.component}
                        </StepContent>
                      </Step>
                    )
                  })}
                </Stepper>
                {activeStep === stepList.length && (
                  <Paper square elevation={0} className={classes.resetContainer}>
                    {
                      isRegisterSuccess
                        ? (
                          <Typography variant='body2' align='center' style={{color: 'green'}}>
                            <p>
                              <CheckIcon style={{fontSize: '3rem'}} />
                            </p>
                            <p>Registration Success !</p>
                            <br />
                            <p>Redirect to your information page in {3} seconds</p>
                          </Typography>
                        )
                        : (
                          <Typography variant='body2' align='center' color='primary'>
                            <div>
                              <CircularProgress size={50} />
                            </div>
                            <p>Registering... please wait for a moment</p>
                          </Typography>
                        )
                    }
                  </Paper>
                )}
              </div>

            }
          </Grid>
        </Grid>
      </div>
    )
  }
}

export default connect()(withStyles(styles)(SigninContent))



