import React, { Component } from 'react'
import { connect } from 'react-redux'
import { reset } from 'redux-form'
import { withRouter } from 'react-router-dom'
import * as actionCreators from '../store/actions'

import SigninContent from '../component/Signin/SigninContent'

class Signin extends Component {
  componentDidMount () {
    const {
      setCurrentRoute,
      getCityAreaList,
      setRegisterSuccess,
      resetForm
    } = this.props
    const formList = ['createAccount', 'generalInfomation', 'uploadAvator']

    setCurrentRoute('/signin')
    getCityAreaList()
    setRegisterSuccess(false)
    formList.forEach(form => resetForm(form))
  }

  componentDidUpdate () {
    const {isRegisterSuccess} = this.props

    if (isRegisterSuccess) {
      setTimeout(() => {
        const {history, createAccount, registerList} = this.props
        const email = createAccount.values.email
        const targetRegister = registerList.find(register => register.email === email) || {}
        console.log(this.props)

        history.push(`/user/${targetRegister.key}`)
      }, 3000)
    }
  }

  render () {
    return (
      <main>
        <SigninContent {...this.props} />
      </main>
    )
  }
}

const mapStateToProps = state => {
  return {
    isDataLoaded: state.action.isDataLoaded,
    isRegisterSuccess: state.action.isRegisterSuccess,
    createAccount: state.form.createAccount,
    generalInfomation: state.form.generalInfomation,
    uploadAvator: state.form.uploadAvator,
    registerList: state.action.registerList
  }
}

const mapDispatchToProps = dispatch => {
  return {
    getRegisterList: (register) => dispatch(actionCreators.getRegisterList(register)),
    addNewRegister: (register) => dispatch(actionCreators.addNewRegister(register)),
    deleteRegisterItem: () => dispatch(actionCreators.deleteRegisterItem()),
    getCityAreaList: () => dispatch(actionCreators.getCityAreaList()),
    setDataLoaded: (isDataLoaded) => dispatch(actionCreators.setDataLoaded(isDataLoaded)),
    setCurrentRoute: (currentRoute) => dispatch(actionCreators.setCurrentRoute(currentRoute)),
    setRegisterSuccess: (isRegisterSuccess) => dispatch(actionCreators.setRegisterSuccess(isRegisterSuccess)),
    resetForm: (formName) => dispatch(reset(formName))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Signin))



