import React from 'react'
import { withStyles } from '@material-ui/core/styles'
import { connect } from 'react-redux'
import { reduxForm } from 'redux-form'

import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import CircularProgress from '@material-ui/core/CircularProgress'

import CloudUploadIcon from '@material-ui/icons/CloudUpload'

import StepButtons from '../StepButtons/StepButtons'

const styles = () => ({
  root: {
    width: '90%',
    height: '30vh',
    border: '.1rem dashed rgba(0,0,0,.3)',
    cursor: 'pointer',
    transition: 'all .3s ease',
    '&:hover': {
      border: '.1rem dashed rgba(0,0,100,.6)'
    },
    '&:hover p': {
      cursor: 'pointer',
      color: 'rgba(0,0,100,.6)',
      transform: 'scale(1.2)'
    }
  },
  hover: {
    transition: 'all .3s ease'
  },
  uploadIcon: {
    fontSize: '3rem'
  },
  avatorArea: {
    position: 'relative',
    textAlign: 'center'
  },
  avator: {
    width: '25vh',
    height: '25vh',
    borderRadius: '50%'
  },
  loaderArea: {
    position: 'absolute',
    width: '25vh',
    height: '25vh',
    top: 'calc(50% - 12.5vh)',
    left: 'calc(50% - 12.5vh)',
    borderRadius: '50%',
    backgroundColor: 'rgba(0,0,0,.5)'
  }
})

const handleUploadChange = (event, change) => {
  if (event.target.files.length === 0) {
    return
  }

  const [file] = event.target.files
  const KB = 1024

  if ((file.size / KB) >= 500) {
    alert('Image is bigger than 500KB!')
  } else {
    const fileReader = new FileReader()

    fileReader.addEventListener('load', () => {
      change('avator', fileReader.result)
      addNewAvator(file, change)
    })
    fileReader.readAsDataURL(file)
  }
}

const addNewAvator = (file, change) => {
  const API_SERVICE = 'https://us-central1-the-f2e-service.cloudfunctions.net'
  const formData = new FormData()
  formData.append('fileToUpload', file)

  fetch(`${API_SERVICE}/uploadFile?dir=ch06-validation`, {
    method: 'post',
    body: formData
  })
    .then(res => res.json())
    .then(data => {
      return new Promise((resolve) => setTimeout(() => resolve(data), 1000))
    })
    .then(data => {
      const {origin, cover, filePath, coverFilePath, originFilePath} = data
      change('avator', origin)
      change('cover', cover)
      change('filePath', {
        originFilePath,
        filePath,
        coverFilePath
      })
    })
}

let UploadAvator = (props) => {
  const {
    classes,
    handleSubmit,
    activeStep,
    handleBack,
    change,
    uploadAvator = {}
  } = props
  const stepButtonsProps = {activeStep, handleBack}
  const isAvatorExist = uploadAvator.values && uploadAvator.values.avator
  const avator = isAvatorExist
    ? uploadAvator.values.avator
    : null
  const isFinish = avator && avator.substr(0, 4) !== 'data'

  return (
    <form onSubmit={handleSubmit}>
      <Grid container justify='center' alignItems='center' className={classes.root}>
        <Grid item xs={12}>
          <label htmlFor='raised-button-file'>
            {
              avator
                ? (
                  <div className={classes.avatorArea}>
                    <img src={avator} alt='avator' className={classes.avator} />
                    {
                      avator && !isFinish
                        ? (
                          <div className={classes.loaderArea}>
                            <CircularProgress style={{marginTop: 'calc(50% - 25px)'}} size={50} />
                          </div>
                        )
                        : null
                    }
                  </div>
                )
                : (
                  <div>
                    <Typography variant='body2' align='center'>
                      <p className={classes.hover}>
                        <CloudUploadIcon className={classes.uploadIcon} />
                      </p>
                      <p className={classes.hover}>Click to upload your avator !</p>
                    </Typography>
                  </div>
                )
            }
          </label>
          <input
            accept='image/*'
            style={{display: 'none'}}
            multiple
            id='raised-button-file'
            type='file'
            onChange={(event) => handleUploadChange(event, change)} />
        </Grid>
      </Grid>
      <StepButtons {...stepButtonsProps} lastStep isFinish={isFinish} />
    </form>
  )
}

UploadAvator = reduxForm({
  form: 'uploadAvator',
  destroyOnUnmount: false
})(UploadAvator)

const mapStateToProps = state => {
  return {
    uploadAvator: state.form.uploadAvator
  }
}

export default connect(mapStateToProps)(withStyles(styles)(UploadAvator))



