import React from 'react'
import { connect } from 'react-redux'
import { withStyles } from '@material-ui/core/styles'
import withWidth from '@material-ui/core/withWidth'
import { Field, reduxForm } from 'redux-form'
import MaskedInput from 'react-text-mask'
import {
  renderField
} from '../../Common/AppFormCollection/AppFormCollection'

import Grid from '@material-ui/core/Grid'

import StepButtons from '../StepButtons/StepButtons'

const validate = values => {
  const errors = {}
  const requiredFields = [ 'name', 'phone', 'city', 'area', 'address' ]
  const {name, phone} = values

  requiredFields.forEach(field => {
    if (!values[field]) {
      errors[field] = 'Required'
    } else {
      switch (field) {
        case 'name':
          if (name.length < 5 || name.length > 15) {
            errors[field] = 'Minimum 5 charaters, Maximum 15 charaters'
          }
          break
        case 'phone':
          if (phone.length !== 13) {
            errors[field] = 'Phone number should have 10 number'
          }
          break
        default:
          break
      }
    }
  })
  return errors
}

const styles = () => ({
  root: {}
})

const TextMaskCustom = (props) => {
  const { inputRef, ...other } = props

  return (
    <MaskedInput
      {...other}
      ref={inputRef}
      mask={['(', /\d/, /\d/, ')', ' ', /\d/, /\d/, /\d/, /\d/, /\d/, /\d/, /\d/, /\d/]}
      guide={false}
      showMask
    />
  )
}

let GeneralInfomation = (props) => {
  const {
    handleSubmit,
    activeStep,
    handleBack,
    invalid,
    cityAreaList,
    generalInfomation = {},
    change,
    width
  } = props
  const stepButtonsProps = {
    activeStep,
    handleBack,
    invalid
  }
  const cityList = cityAreaList.map(cityArea => {
    return {
      name: cityArea.city,
      value: cityArea.city
    }
  })
  const isCityExist = generalInfomation.values && generalInfomation.values.city
  const targetCityArea = isCityExist
    ? cityAreaList.find(cityArea => cityArea.city === generalInfomation.values.city)
    : null
  const areaList = targetCityArea
    ? targetCityArea.areaList.map(area => {
      return {
        name: area.areaName,
        value: `${area.zipCode}|${area.areaName}`
      }
    })
    : []
  const isAreaExist = generalInfomation.values && generalInfomation.values.area
  const [addressZipCode, addressAreaName] = isAreaExist
    ? generalInfomation.values.area.split('|')
    : []

  return (
    <form onSubmit={handleSubmit}>
      <div>
        <Field name='name' label='Name' component={renderField} required />
      </div>
      <div>
        <Field
          name='phone'
          label='Phone'
          component={renderField}
          inputComponent={TextMaskCustom}
          required />
      </div>
      <div>
        <Grid container spacing={8}>
          <Grid item xs={6}>
            <Field
              name='city'
              label='City'
              fieldType='select'
              component={renderField}
              optionList={cityList}
              onSelectChange={(event) => {
                change('area', '')
              }}
              required />
          </Grid>
          <Grid item xs={6}>
            <Field
              name='area'
              label='Area'
              fieldType='select'
              component={renderField}
              optionList={areaList}
              required />
          </Grid>
        </Grid>
      </div>
      <div>
        <Field
          name='address'
          label='Address'
          component={renderField}
          disabled={!isAreaExist}
          startAdornment={
            isAreaExist && `${width !== 'xs' ? `(${addressZipCode})` : ''}${targetCityArea.city}${addressAreaName}`
          }
          required />
      </div>
      <StepButtons {...stepButtonsProps} />
    </form>
  )
}

GeneralInfomation = reduxForm({
  form: 'generalInfomation',
  validate,
  destroyOnUnmount: false
})(GeneralInfomation)

const mapStateToProps = state => {
  return {
    cityAreaList: state.action.cityAreaList,
    generalInfomation: state.form.generalInfomation
  }
}

export default connect(mapStateToProps)(withWidth()(withStyles(styles)(GeneralInfomation)))



