import React from 'react'
import { withStyles } from '@material-ui/core/styles'
import { connect } from 'react-redux'
import { Field, reduxForm, formValueSelector } from 'redux-form'

import {
  renderField
} from '../../Common/AppFormCollection/AppFormCollection'

import StepButtons from '../StepButtons/StepButtons'

const validate = values => {
  const errors = {}
  const requiredFields = [ 'email', 'password', 'confirmPassword' ]
  const {email, password, confirmPassword} = values

  requiredFields.forEach(field => {
    if (!values[field]) {
      errors[field] = 'Required'
    } else {
      switch (field) {
        case 'email':
          if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(email)) {
            errors[field] = 'Invalid email address'
          }
          break
        case 'password':
          if (!/^([a-zA-Z0-9_-]){8,12}$/.test(password)) {
            errors[field] = 'Minimum 8 charaters, Maximum 12 charaters'
          }
          break
        case 'confirmPassword':
          if (confirmPassword !== password) {
            errors[field] = 'Password not match'
          }
          break
        default:
          break
      }
    }
  })
  return errors
}

const asyncValidate = (values) => {
  const {email} = values
  const API_URL = 'https://the-f2e-service.firebaseio.com/validation/registerList.json'
  const API_QUERY = `${API_URL}?orderBy="email"&equalTo="${email}"`
  return fetch(API_QUERY)
    .then(res => res.json())
    .then(data => {
      if (Object.keys(data).length > 0) {
        throw JSON.parse(JSON.stringify({email: `Email is exist !`}))
      }
    })
}

const styles = theme => ({
  root: {},
  formControl: {
    margin: theme.spacing.unit
  }
})

let CreateAccount = (props) => {
  const { handleSubmit, activeStep, handleBack, invalid } = props
  const stepButtonsProps = {
    activeStep,
    handleBack,
    invalid
  }
  const inputList = [
    {name: 'email', label: 'Email', type: 'text'},
    {name: 'password', label: 'Password', type: 'password'},
    {name: 'confirmPassword', label: 'Confirm Password', type: 'password'}
  ]

  return (
    <form onSubmit={handleSubmit}>
      {inputList.map((input, index) => {
        const {name, label, type} = input

        return (
          <div key={index}>
            <Field
              name={name}
              label={label}
              type={type}
              required
              component={renderField} />
          </div>
        )
      })}
      <StepButtons {...stepButtonsProps} />
    </form>
  )
}

CreateAccount = reduxForm({
  form: 'createAccount',
  validate,
  asyncValidate,
  asyncBlurFields: [ 'email' ],
  destroyOnUnmount: false
})(CreateAccount)

const selector = formValueSelector('createAccount')
CreateAccount = connect(
  state => {
    const emailValue = selector(state, 'email')
    const passwordValue = selector(state, 'password')
    const confirmPasswordValue = selector(state, 'confirmPassword')

    return {
      formValues: {
        emailValue,
        passwordValue,
        confirmPasswordValue
      }
    }
  }
)(CreateAccount)

export default withStyles(styles)(CreateAccount)



